import * as React from 'react'
import { getDayStr } from '../utils'
import Cookies from 'js-cookie';

const GameContext = React.createContext();

let DEFAULT_TODAY_GUEST_LIST = [{
    isCorrect: false,
    isSkipped: false,
    answer: ""
},
{
    isCorrect: false,
    isSkipped: false,
    answer: ""
},
{
    isCorrect: false,
    isSkipped: false,
    answer: ""
},
{
    isCorrect: false,
    isSkipped: false,
    answer: ""
},
{
    isCorrect: false,
    isSkipped: false,
    answer: ""
},
{
    isCorrect: false,
    isSkipped: false,
    answer: ""
}];

const MAX_GUESS_INDEX = 5;


function saveState(state) {
    let now = new Date();

    let stateKey = state.day || getDayStr();
    state.ls = now.getTime();

    localStorage.setItem(stateKey, JSON.stringify(state));
}

function loadState() {
    let stateKey = getDayStr();

    let state = localStorage.getItem(stateKey);
    if (state) {
        return JSON.parse(state);
    }

    return {
        guessList: DEFAULT_TODAY_GUEST_LIST,
        lastStep: 0,
        openedStep: 0,
        finished: false
    }
}

function saveMaxStreak(currentStreak, gameWin) {
    let maxStreak = parseInt(Cookies.get('max-streak') || 0);
    let allWins = parseInt(Cookies.get('all-wins') || 0);
    if (gameWin === false) { // usuário errou o último jogo e resetou a currentStreak
        allWins = allWins + currentStreak;
    } else {
        if (currentStreak > maxStreak) {
            const now = new Date();
            const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);
            Cookies.set('max-streak', currentStreak, { expires });
        }
        allWins = allWins + 1;
        const now = new Date();
        const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);
        Cookies.set('all-wins', allWins, { expires });
    }
}



function modalReducer(state, action) {
    let latestState = state;

    switch (action.type) {
        case 'SKIP': {
            let guessList = state.guessList;
            let lastStep = state.lastStep;
            let openedStep = state.openedStep;
            let wrongAttempts = parseInt(Cookies.get('wrong-attempts')) || 0;

            const now = new Date();
            const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);
            // Increment the number of wrong attempts and save it in a cookie
            Cookies.set('wrong-attempts', wrongAttempts + 1, { expires });

            guessList[lastStep].isSkipped = true;

            let finished = state.finished;
            if (lastStep !== MAX_GUESS_INDEX) {
                lastStep = lastStep + 1
            } else {
                finished = true
                Cookies.set('wrongOnes', parseInt(Cookies.get('wrongOnes') || 0) + 1, { expires: 5000 });
                Cookies.set('wrong-attempts', 0, { expires });
                Cookies.set('no-win', 7, { expires });
            }

            latestState = {
                ...state,
                guessList: guessList,
                lastStep: lastStep,
                openedStep: openedStep + 1,
                finished: finished
            }
            break
        }
        case 'SUBMIT-WRONG': {
            let guessList = state.guessList;
            let lastStep = state.lastStep;
            let openedStep = state.openedStep;
            let wrongAttempts = parseInt(Cookies.get('wrong-attempts')) || 0;

            const now = new Date();
            const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);

            guessList[lastStep].answer = action.payload.answer;

            let finished = state.finished;
            if (lastStep !== MAX_GUESS_INDEX) {
                lastStep = lastStep + 1
                Cookies.set('wrong-attempts', wrongAttempts + 1, { expires });
            } else {
                finished = true

                Cookies.set('wrongOnes', parseInt(Cookies.get('wrongOnes') || 0) + 1, { expires: 5000 });

                const now = new Date();
                const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);
                // Increment the number of wrong attempts and save it in a cookie
                wrongAttempts = parseInt(wrongAttempts) + 1;
                Cookies.set('wrong-attempts', wrongAttempts + 1, { expires });


                // get the current values of game-wins and max-streak cookies
                const gameWins = parseInt(Cookies.get('game-wins') || 0);
                const maxStreak = parseInt(Cookies.get('max-streak') || 0);

                // update the game-wins and game-played cookies
                Cookies.set('game-wins', 0, { expires });
                Cookies.set('no-win', 7, { expires });
                Cookies.set('game-played', parseInt(Cookies.get('game-played') || 0) + 1, { expires });

                // update the max-streak cookie if necessary
                if (gameWins > maxStreak) {
                    Cookies.set('max-streak', gameWins, { expires });
                }
            }
        
            latestState = {
                ...state,
                guessList: guessList,
                lastStep: lastStep,
                finished: finished,
                openedStep: openedStep + 1,
            };

            break
        }
        case 'SUBMIT-CORRRECT': {
            let guessList = state.guessList;
            let lastStep = state.lastStep;
            let openedStep = state.openedStep;
            let wrongAttempts = parseInt(Cookies.get('wrong-attempts')) || 0;

            guessList[lastStep].isCorrect = true;
            guessList[lastStep].answer = action.payload.answer;

            if (lastStep !== MAX_GUESS_INDEX) {
                lastStep = lastStep + 1
            }

            latestState = {
                ...state,
                guessList: guessList,
                lastStep: lastStep + 1,
                openedStep: openedStep + 1,
                finished: true
            };

            const now = new Date();
            const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);

            // Save data to cookies
            Cookies.set('game-wins', parseInt(Cookies.get('game-wins') || 0) + 1, { expires });
            Cookies.set('game-played', parseInt(Cookies.get('game-played') || 0) + 1, { expires });

            // Save the number of wrong attempts to a cookie
            Cookies.set('last-win-attempts', wrongAttempts + 1, { expires });

            // Check if the number of attempts is equal to 1, 2, 3, 4, 5 or 6
            let attempts = wrongAttempts + 1;
            if (attempts === 1 || attempts === 2 || attempts === 3 || attempts === 4 || attempts === 5 || attempts === 6) {
                let winAttempts = parseInt(Cookies.get(`win-${attempts}`)) || 0;
                Cookies.set(`win-${attempts}`, winAttempts + 1, { expires: 5000 });
            }

            // Reset the wrong-attempts cookie
            Cookies.set('wrong-attempts', 0);
            Cookies.set('no-win', 0);

            let currentStreak = parseInt(Cookies.get('game-wins') || 0);
            saveMaxStreak(currentStreak);
            break
        }
        case 'FINISH': {
            let guessList = state.guessList;
            guessList[MAX_GUESS_INDEX].isSkipped = true;

            // get the current values of game-wins and max-streak cookies
            const gameWins = parseInt(Cookies.get('game-wins') || 0);
            const maxStreak = parseInt(Cookies.get('max-streak') || 0);

            const now = new Date();
            const expires = new Date(now.getTime() + 5000 * 24 * 60 * 60 * 1000);

            // update the game-wins and game-played cookies
            Cookies.set('game-wins', 0, { expires });
            Cookies.set('game-played', parseInt(Cookies.get('game-played') || 0) + 1, { expires });

            // update the max-streak cookie if necessary
            if (gameWins > maxStreak) {
                Cookies.set('max-streak', gameWins, { expires });
            }

            // Reset the wrong-attempts cookie
            Cookies.set('wrongOnes', parseInt(Cookies.get('wrongOnes') || 0) + 1, { expires: 5000 });
            Cookies.set('wrong-attempts', 0, { expires: 1 });
            Cookies.set('no-win', 7, { expires: 1 });
            Cookies.set('last-win-attempts', 0, { expires: 1 });

            latestState = {
                ...state,
                guessList: guessList,
                lastStep: MAX_GUESS_INDEX,
                finished: true
            };

            break
        }
        case 'RESET': {
            //load from localstorage
            latestState = {
                ...state,
                guessList: DEFAULT_TODAY_GUEST_LIST,
                lastStep: 0,
                openedStep: 0,
                finished: false
            }
            break
        }
        case 'SAVE': {
            //load from localstorage
            latestState = {
                ...state
            }
            break
        }
        default: {
            console.error(`Unhandled action type: ${action.type}`)
            latestState = {
                guessList: DEFAULT_TODAY_GUEST_LIST,
                lastStep: 0,
                openedStep: 0,
                finished: false
            }
            break
        }
    }

    saveState(latestState);

    return latestState;
}

function GameContextProvider({ children }) {
    //load from localstorage
    const [state, dispatch] = React.useReducer(modalReducer, loadState())
    const value = { state, dispatch }
    return <GameContext.Provider value={value}>{children}</GameContext.Provider>
}

function useGameData() {
    const context = React.useContext(GameContext)
    if (context === undefined) {
        throw new Error('useGameData must be used within a GameContextProvider')
    }
    return context
}


export { GameContextProvider, useGameData }